/**
 * Ship - Represents player and enemy ships with their properties and behaviors
 */
export class Ship {
    constructor(config) {
        // Position and size
        this.x = config.x;
        this.y = config.y;
        this.width = config.width;
        this.height = config.height;
        
        // Health and energy
        this.health = config.health || 100;
        this.maxHealth = config.maxHealth || this.health;
        this.energy = config.energy || 100;
        this.maxEnergy = config.maxEnergy || this.energy;
        
        // Movement properties
        this.velocityX = config.velocityX || 0.8;
        this.direction = config.direction || 1; // 1 for right, -1 for left
        this.minX = config.minX || 60;
        this.maxX = config.maxX || 740;
        
        // Ship type
        this.isPlayer = config.isPlayer || false;
        this.isEnemy = config.isEnemy || false;
    }

    // Update ship position and handle boundary collisions
    update() {
        // Update position
        this.x += this.velocityX * this.direction;
        
        // Bounce off boundaries
        if (this.x <= this.minX) {
            this.x = this.minX;
            this.direction = 1; // Change direction to right
        } else if (this.x >= this.maxX) {
            this.x = this.maxX;
            this.direction = -1; // Change direction to left
        }
    }

    // Take damage
    takeDamage(damage) {
        this.health -= damage;
        this.health = Math.max(0, this.health);
        
        console.log(`${this.isPlayer ? 'Player' : 'Enemy'} took ${damage} damage! Health: ${this.health}`);
        
        return this.health <= 0; // Return true if destroyed
    }

    // Repair hull (used by math challenge system)
    repairHull(amount = 50) {
        this.health = Math.min(this.maxHealth, this.health + amount);
        console.log(`Hull repaired! Health: ${this.health}`);
    }

    // Recharge energy (used by math challenge system)
    rechargeEnergy(amount = 50) {
        if (this.isPlayer) {
            this.energy = Math.min(this.maxEnergy, this.energy + amount);
            console.log(`Energy recharged! Energy: ${this.energy}`);
        }
    }

    // Consume energy (for weapons)
    consumeEnergy(amount) {
        if (this.isPlayer) {
            this.energy -= amount;
            this.energy = Math.max(0, this.energy);
            return this.energy >= 0;
        }
        return true; // Enemy ships don't use energy system
    }

    // Check if ship has enough energy for an action
    hasEnergy(amount) {
        if (this.isPlayer) {
            return this.energy >= amount;
        }
        return true; // Enemy ships don't use energy system
    }

    // Get collision bounds
    getBounds() {
        return {
            left: this.x - this.width/2,
            right: this.x + this.width/2,
            top: this.y - this.height/2,
            bottom: this.y + this.height/2
        };
    }

    // Check if a point is inside the ship
    contains(x, y) {
        const bounds = this.getBounds();
        return x >= bounds.left && x <= bounds.right &&
               y >= bounds.top && y <= bounds.bottom;
    }

    // Reset ship to initial state
    reset(config) {
        this.x = config.x;
        this.y = config.y;
        this.health = config.health || this.maxHealth;
        this.energy = config.energy || this.maxEnergy;
        this.direction = config.direction || 1;
    }

    // Level up enemy ship (increase stats)
    levelUp(level) {
        if (this.isEnemy) {
            this.maxHealth = 100 + (level - 1) * 25;
            this.health = this.maxHealth;
            console.log(`Enemy leveled up! Level ${level}, Health: ${this.maxHealth}`);
        }
    }

    // Get health percentage
    getHealthPercent() {
        return (this.health / this.maxHealth) * 100;
    }

    // Get energy percentage  
    getEnergyPercent() {
        return (this.energy / this.maxEnergy) * 100;
    }

    // Check if ship is destroyed
    isDestroyed() {
        return this.health <= 0;
    }

    // Check if ship is alive
    isAlive() {
        return this.health > 0;
    }
}

// Factory functions for creating specific ship types
export function createPlayerShip(canvasWidth, canvasHeight) {
    return new Ship({
        x: canvasWidth / 2,
        y: canvasHeight - 120,
        width: 120,
        height: 90,
        health: 100,
        energy: 100,
        maxEnergy: 100,
        velocityX: 0.8,
        direction: 1,
        minX: 60,
        maxX: canvasWidth - 60,
        isPlayer: true
    });
}

export function createEnemyShip(canvasWidth, canvasHeight, level = 1) {
    const maxHealth = 100 + (level - 1) * 25;
    return new Ship({
        x: canvasWidth / 2,
        y: 90,
        width: 120,
        height: 90,
        health: maxHealth,
        maxHealth: maxHealth,
        velocityX: 0.6,
        direction: -1,
        minX: 60,
        maxX: canvasWidth - 60,
        isEnemy: true
    });
}
