/**
 * AssetManager - Handles loading and managing game assets (images and sounds)
 */
export class AssetManager {
    constructor() {
        this.images = {};
        this.sounds = {};
        this.loadingComplete = false;
        this.loadPromises = [];
        
        this.initializeAssets();
    }

    initializeAssets() {
        // Get the base URL from Vite (will be / in dev, /star_trek/ in production)
        const baseUrl = import.meta.env.BASE_URL;
        
        // Load images with promises
        this.images = {
            playerShip: this.loadImage(`${baseUrl}assets/images/player-ship.png`),
            enemyShip: this.loadImage(`${baseUrl}assets/images/opponent-ship.png`),
        };

        // Load sounds with promises
        this.sounds = {
            photon: this.loadSound(`${baseUrl}assets/sounds/photon_torpedo.mp3`),
            phaser: this.loadSound(`${baseUrl}assets/sounds/phasers.mp3`),
            quantum: this.loadSound(`${baseUrl}assets/sounds/quantum_torpedo.mp3`),
            romulanDisruptor: this.loadSound(`${baseUrl}assets/sounds/romulan_disruptor.mp3`),
            romulanTorpedo: this.loadSound(`${baseUrl}assets/sounds/romulan_torpedo.mp3`),
            explosion1: this.loadSound(`${baseUrl}assets/sounds/smallexplosion1.mp3`),
            explosion2: this.loadSound(`${baseUrl}assets/sounds/smallexplosion2.mp3`),
        };

        // Use promise-based loading instead of polling
        this.waitForAssets().then(() => {
            this.loadingComplete = true;
            console.log('All assets loaded successfully!');
        }).catch(error => {
            console.error('Error loading assets:', error);
        });
    }

    loadImage(src) {
        const img = new Image();
        
        const loadPromise = new Promise((resolve, reject) => {
            img.onload = () => {
                console.log(`Image loaded: ${src}`);
                resolve(img);
            };
            img.onerror = (e) => {
                console.error(`Image load error for ${src}:`, e);
                reject(e);
            };
        });
        
        this.loadPromises.push(loadPromise);
        img.src = src;
        return img;
    }

    loadSound(src) {
        const audio = new Audio(src);
        audio.volume = 0.3; // 30% volume to avoid being too loud
        
        const loadPromise = new Promise((resolve, reject) => {
            audio.addEventListener('canplaythrough', () => {
                console.log(`Sound loaded: ${src}`);
                resolve(audio);
            });
            audio.addEventListener('error', (e) => {
                console.error(`Sound load error for ${src}:`, e);
                reject(e);
            });
        });
        
        this.loadPromises.push(loadPromise);
        audio.load(); // Start loading
        return audio;
    }

    checkAssetsLoaded() {
        const allImagesLoaded = Object.values(this.images).every(img => img.complete);
        
        if (allImagesLoaded && !this.loadingComplete) {
            this.loadingComplete = true;
            console.log('All assets loaded successfully!');
            return true;
        } else if (!allImagesLoaded) {
            // Check again in 100ms
            setTimeout(() => this.checkAssetsLoaded(), 100);
            return false;
        }
        
        return this.loadingComplete;
    }

    // Promise-based asset loading
    async waitForAssets() {
        try {
            await Promise.all(this.loadPromises);
            this.loadingComplete = true;
            console.log('All assets loaded via promises!');
            return true;
        } catch (error) {
            console.error('Error loading assets:', error);
            return false;
        }
    }

    getImage(name) {
        return this.images[name];
    }

    getSound(name) {
        return this.sounds[name];
    }

    playSound(name) {
        const sound = this.sounds[name];
        if (sound) {
            console.log(`Playing ${name} sound...`);
            sound.currentTime = 0; // Reset to beginning
            sound.play().catch(error => {
                console.error(`Sound play failed for ${name}:`, error);
            });
        } else {
            console.error(`Sound not found: ${name}`);
        }
    }

    playWeaponSound(weaponType) {
        console.log(`${weaponType.toUpperCase()} FIRED!`);
        this.playSound(weaponType);
    }

    playEnemyWeaponSound(weaponType) {
        console.log(`ENEMY ${weaponType.toUpperCase()} FIRED!`);
        
        // Map enemy weapon types to Romulan sounds
        const soundMap = {
            'phaser': 'romulanDisruptor',
            'torpedo': 'romulanTorpedo'
        };
        
        const soundKey = soundMap[weaponType];
        if (soundKey) {
            this.playSound(soundKey);
        } else {
            console.error(`Enemy sound not found for weapon type: ${weaponType}`);
        }
    }

    playExplosionSound() {
        // Randomly select between the two explosion sounds
        const explosionSounds = ['explosion1', 'explosion2'];
        const randomSound = explosionSounds[Math.floor(Math.random() * explosionSounds.length)];
        this.playSound(randomSound);
    }

    isLoaded() {
        return this.loadingComplete;
    }

    // Get all assets for backwards compatibility
    getAssets() {
        return {
            images: this.images,
            sounds: this.sounds
        };
    }
}
