/**
 * GameRenderer - Handles all drawing and rendering operations
 */
export class GameRenderer {
    constructor(canvas, assetManager) {
        this.canvas = canvas;
        this.ctx = canvas.getContext('2d');
        this.width = canvas.width;
        this.height = canvas.height;
        this.assetManager = assetManager;
        
        // Initialize starfield
        this.stars = [];
        this.initializeStars();
    }

    initializeStars() {
        for (let i = 0; i < 200; i++) {
            this.stars.push({
                x: Math.random() * this.width,
                y: Math.random() * this.height,
                size: Math.random() * 2 + 0.5,
                speed: Math.random() * 0.5 + 0.1,
                brightness: Math.random() * 0.8 + 0.2
            });
        }
    }

    updateStars() {
        this.stars.forEach(star => {
            star.y += star.speed;
            if (star.y > this.height) {
                star.y = 0;
                star.x = Math.random() * this.width;
            }
        });
    }

    render(gameState) {
        // Clear canvas
        this.ctx.fillStyle = '#000';
        this.ctx.fillRect(0, 0, this.width, this.height);
        
        // Draw stars
        this.drawStars();
        
        // Show loading message if assets aren't ready
        if (!this.assetManager.isLoaded()) {
            this.drawLoadingScreen();
            return;
        }
        
        // Draw ships
        this.drawShips(gameState.playerShip, gameState.enemyShip);
        
        // Draw projectiles
        this.drawProjectiles(gameState.weaponSystem.getProjectiles());
        
        // Draw miss messages
        this.drawMissMessages(gameState.weaponSystem.getMissMessages());
        
        // Draw ship explosions
        this.drawShipExplosions(gameState.weaponSystem.getShipExplosions());
        
        // Draw game over screen if needed
        if (gameState.gameOver) {
            this.drawGameOverScreen(gameState);
        }
    }

    drawLoadingScreen() {
        this.ctx.fillStyle = '#00ff00';
        this.ctx.font = '24px Courier New';
        this.ctx.textAlign = 'center';
        this.ctx.fillText('Loading Assets...', this.width / 2, this.height / 2);
        this.ctx.textAlign = 'left';
    }

    drawStars() {
        this.ctx.fillStyle = '#ffffff';
        this.stars.forEach(star => {
            this.ctx.globalAlpha = star.brightness;
            this.ctx.fillRect(star.x, star.y, star.size, star.size);
        });
        this.ctx.globalAlpha = 1;
    }

    drawShips(playerShip, enemyShip) {
        // Draw player ship (only if alive)
        if (playerShip.isAlive()) {
            const playerImage = this.assetManager.getImage('playerShip');
            if (playerImage && playerImage.complete) {
                this.drawShipWithTransparency(
                    playerImage,
                    playerShip.x - playerShip.width/2,
                    playerShip.y - playerShip.height/2,
                    playerShip.width,
                    playerShip.height,
                    '#00ffff'
                );
            } else {
                // Fallback rectangle
                this.ctx.fillStyle = '#00ff00';
                this.ctx.fillRect(
                    playerShip.x - playerShip.width/2,
                    playerShip.y - playerShip.height/2,
                    playerShip.width,
                    playerShip.height
                );
            }
        }
        
        // Draw enemy ship (only if alive)
        if (enemyShip.isAlive()) {
            const enemyImage = this.assetManager.getImage('enemyShip');
            if (enemyImage && enemyImage.complete) {
                this.drawShipWithTransparency(
                    enemyImage,
                    enemyShip.x - enemyShip.width/2,
                    enemyShip.y - enemyShip.height/2,
                    enemyShip.width,
                    enemyShip.height,
                    '#ff6600'
                );
            } else {
                // Fallback rectangle
                this.ctx.fillStyle = '#ff0000';
                this.ctx.fillRect(
                    enemyShip.x - enemyShip.width/2,
                    enemyShip.y - enemyShip.height/2,
                    enemyShip.width,
                    enemyShip.height
                );
            }
        }
    }

    drawShipWithTransparency(image, x, y, width, height, glowColor) {
        // Use a cached processed image to avoid reprocessing every frame
        const cacheKey = `${image.src}_${width}_${height}`;
        
        if (!this.processedImageCache) {
            this.processedImageCache = new Map();
        }
        
        let processedCanvas = this.processedImageCache.get(cacheKey);
        
        if (!processedCanvas) {
            // Create and cache the processed image
            processedCanvas = document.createElement('canvas');
            const tempCtx = processedCanvas.getContext('2d');
            processedCanvas.width = width;
            processedCanvas.height = height;
            
            // Draw the image to temp canvas
            tempCtx.drawImage(image, 0, 0, width, height);
            
            // Get image data to process transparency
            const imageData = tempCtx.getImageData(0, 0, width, height);
            const data = imageData.data;
            
            // Remove background (make white/light colors transparent)
            for (let i = 0; i < data.length; i += 4) {
                const r = data[i];
                const g = data[i + 1];
                const b = data[i + 2];
                
                // Check if pixel is close to white/background color
                const brightness = (r + g + b) / 3;
                const isBackground = brightness > 200 && 
                                   Math.abs(r - g) < 30 && 
                                   Math.abs(g - b) < 30 && 
                                   Math.abs(r - b) < 30;
                
                if (isBackground) {
                    data[i + 3] = 0; // Make transparent
                }
            }
            
            // Put the processed data back
            tempCtx.putImageData(imageData, 0, 0);
            
            // Cache the processed canvas
            this.processedImageCache.set(cacheKey, processedCanvas);
        }
        
        // Draw the cached processed image to main canvas
        this.ctx.drawImage(processedCanvas, x, y);
    }

    drawProjectiles(projectiles) {
        projectiles.forEach(projectile => {
            if (projectile.type === 'phaser' || projectile.type === 'enemy_phaser') {
                this.drawPhaserBeam(projectile);
            } else if (projectile.type === 'explosion') {
                this.drawExplosionParticle(projectile);
            } else {
                this.drawTorpedo(projectile);
            }
        });
    }

    drawPhaserBeam(beam) {
        const elapsed = Date.now() - beam.startTime;
        const alpha = 1 - (elapsed / beam.duration);
        
        // Calculate current positions from ship references
        const isEnemy = beam.shooterShip.isEnemy;
        const startX = beam.shooterShip.x;
        const startY = isEnemy ? 
            beam.shooterShip.y + beam.shooterShip.height/2 : 
            beam.shooterShip.y - beam.shooterShip.height/2;
        const endX = beam.targetShip.x;
        const endY = beam.targetShip.y;
        
        // Choose color based on beam type
        const color = beam.type === 'enemy_phaser' ? '#00ff00' : '#ff0000';
        
        this.ctx.globalAlpha = alpha;
        this.ctx.strokeStyle = color;
        this.ctx.lineWidth = 4;
        this.ctx.shadowColor = color;
        this.ctx.shadowBlur = 10;
        
        this.ctx.beginPath();
        this.ctx.moveTo(startX, startY);
        this.ctx.lineTo(endX, endY);
        this.ctx.stroke();
        
        this.ctx.shadowBlur = 0;
        this.ctx.globalAlpha = 1;
    }

    drawExplosionParticle(particle) {
        const alpha = particle.life / particle.maxLife;
        this.ctx.globalAlpha = alpha;
        this.ctx.fillStyle = `hsl(${30 + Math.random() * 30}, 100%, 50%)`;
        this.ctx.fillRect(particle.x, particle.y, particle.size, particle.size);
        this.ctx.globalAlpha = 1;
    }

    drawTorpedo(projectile) {
        // Draw trail as small circles
        if (projectile.trail) {
            projectile.trail.forEach((point, index) => {
                const alpha = index / projectile.trail.length * 0.5;
                this.ctx.globalAlpha = alpha;
                this.ctx.fillStyle = projectile.color;
                
                this.ctx.beginPath();
                this.ctx.arc(point.x, point.y, 1.5, 0, Math.PI * 2);
                this.ctx.fill();
            });
            this.ctx.globalAlpha = 1;
        }
        
        // Draw particles for quantum torpedoes
        if (projectile.particles) {
            projectile.particles.forEach(particle => {
                const alpha = particle.life / particle.maxLife;
                this.ctx.globalAlpha = alpha;
                this.ctx.fillStyle = projectile.color;
                
                this.ctx.beginPath();
                this.ctx.arc(particle.x, particle.y, 1, 0, Math.PI * 2);
                this.ctx.fill();
            });
            this.ctx.globalAlpha = 1;
        }
        
        // Draw main projectile as a circle
        this.ctx.fillStyle = projectile.color;
        this.ctx.shadowColor = projectile.color;
        this.ctx.shadowBlur = 10;
        
        const radius = Math.max(projectile.width, projectile.height) / 2;
        
        this.ctx.beginPath();
        this.ctx.arc(projectile.x, projectile.y, radius, 0, Math.PI * 2);
        this.ctx.fill();
        
        // Add bright center glow
        this.ctx.shadowBlur = 5;
        this.ctx.fillStyle = 'rgba(255, 255, 255, 0.8)';
        this.ctx.beginPath();
        this.ctx.arc(projectile.x, projectile.y, radius * 0.4, 0, Math.PI * 2);
        this.ctx.fill();
        
        this.ctx.shadowBlur = 0;
    }

    drawMissMessages(missMessages) {
        missMessages.forEach(message => {
            const alpha = message.life / message.maxLife;
            
            this.ctx.globalAlpha = alpha;
            this.ctx.fillStyle = message.color;
            this.ctx.font = `bold ${message.fontSize}px Courier New`;
            this.ctx.textAlign = 'left';
            this.ctx.shadowColor = message.color;
            this.ctx.shadowBlur = 8;
            
            this.ctx.fillText(message.text, message.x, message.y);
            
            this.ctx.globalAlpha = 1;
            this.ctx.shadowBlur = 0;
            this.ctx.textAlign = 'left';
        });
    }

    drawShipExplosions(shipExplosions) {
        shipExplosions.forEach(explosion => {
            explosion.particles.forEach(particle => {
                const alpha = particle.life / particle.maxLife;
                
                this.ctx.globalAlpha = alpha;
                this.ctx.fillStyle = particle.color;
                this.ctx.shadowColor = particle.color;
                this.ctx.shadowBlur = 8;
                
                this.ctx.beginPath();
                this.ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2);
                this.ctx.fill();
                
                // Add bright center for dramatic effect
                if (alpha > 0.5) {
                    this.ctx.fillStyle = '#ffffff';
                    this.ctx.globalAlpha = alpha * 0.8;
                    this.ctx.beginPath();
                    this.ctx.arc(particle.x, particle.y, particle.size * 0.4, 0, Math.PI * 2);
                    this.ctx.fill();
                }
            });
        });
        
        this.ctx.globalAlpha = 1;
        this.ctx.shadowBlur = 0;
    }

    drawGameOverScreen(gameState) {
        // Semi-transparent overlay
        this.ctx.fillStyle = 'rgba(0, 0, 0, 0.8)';
        this.ctx.fillRect(0, 0, this.width, this.height);
        
        // Game Over title
        this.ctx.fillStyle = '#ff0000';
        this.ctx.font = 'bold 48px Courier New';
        this.ctx.textAlign = 'center';
        this.ctx.shadowColor = '#ff0000';
        this.ctx.shadowBlur = 15;
        this.ctx.fillText(gameState.gameOverMessage, this.width / 2, this.height / 2 - 40);
        
        // Subtitle
        this.ctx.fillStyle = '#ffffff';
        this.ctx.font = '24px Courier New';
        this.ctx.shadowColor = '#ffffff';
        this.ctx.shadowBlur = 8;
        this.ctx.fillText(`Hull Integrity: ${gameState.playerShip.health}%`, this.width / 2, this.height / 2 + 20);
        this.ctx.fillText(`Final Level Reached: ${gameState.level}`, this.width / 2, this.height / 2 + 60);
        
        // Restart instruction
        this.ctx.fillStyle = '#00ff00';
        this.ctx.font = '20px Courier New';
        this.ctx.shadowColor = '#00ff00';
        this.ctx.shadowBlur = 10;
        this.ctx.fillText('Click RESTART to begin new mission', this.width / 2, this.height / 2 + 120);
        
        // Reset text styling
        this.ctx.textAlign = 'left';
        this.ctx.shadowBlur = 0;
    }

    // Update animations (call this every frame)
    update() {
        this.updateStars();
    }
}
