/**
 * GameUI - Handles UI updates and management
 */
export class GameUI {
    constructor() {
        this.weaponConfigs = {
            photon: 25,
            phaser: 50,
            quantum: 50
        };
        
        this.initializeEventListeners();
    }

    initializeEventListeners() {
        // These will be set by the main game class
        this.weaponCallbacks = {};
        this.repairCallbacks = {};
        this.gameCallbacks = {};
    }

    // Set callback functions for weapon firing
    setWeaponCallbacks(callbacks) {
        this.weaponCallbacks = callbacks;
        
        const photonBtn = document.getElementById('photonBtn');
        const phaserBtn = document.getElementById('phaserBtn');
        const quantumBtn = document.getElementById('quantumBtn');
        
        photonBtn.addEventListener('click', () => this.fireWeapon('photon'));
        phaserBtn.addEventListener('click', () => this.fireWeapon('phaser'));
        quantumBtn.addEventListener('click', () => this.fireWeapon('quantum'));
    }

    // Set callback functions for repair actions
    setRepairCallbacks(callbacks) {
        this.repairCallbacks = callbacks;
        
        const repairHullBtn = document.getElementById('repairHullBtn');
        const repairEnergyBtn = document.getElementById('repairEnergyBtn');
        
        repairHullBtn.addEventListener('click', () => this.showMathChallenge('hull'));
        repairEnergyBtn.addEventListener('click', () => this.showMathChallenge('energy'));
    }

    // Set callback functions for game actions
    setGameCallbacks(callbacks) {
        this.gameCallbacks = callbacks;
        
        const restartBtn = document.getElementById('restartBtn');
        const continueBtn = document.getElementById('continueBtn');
        const startGameBtn = document.getElementById('startGameBtn');
        
        restartBtn.addEventListener('click', () => {
            this.gameCallbacks.restart();
            this.hideRestartButton();
        });
        
        continueBtn.addEventListener('click', () => {
            this.gameCallbacks.continue();
            this.hideContinueButton();
        });
        
        startGameBtn.addEventListener('click', () => this.gameCallbacks.start());
    }

    fireWeapon(weaponType) {
        if (this.weaponCallbacks.fire) {
            this.weaponCallbacks.fire(weaponType);
        }
        
        // Add visual feedback
        const button = document.getElementById(weaponType + 'Btn');
        button.classList.add('firing');
        setTimeout(() => button.classList.remove('firing'), 500);
    }

    showMathChallenge(repairType) {
        if (this.repairCallbacks.show) {
            this.repairCallbacks.show(repairType);
        }
    }

    // Update all UI elements
    updateUI(gameState) {
        this.updateHealthBars(gameState);
        this.updateWeaponStatus(gameState.playerShip);
        this.updateLevelDisplay(gameState.level);
        this.updateEnemyLabel(gameState.level);
    }

    updateHealthBars(gameState) {
        // Update player health bar
        const hullBar = document.querySelector('.hull-integrity');
        if (hullBar) {
            hullBar.style.width = gameState.playerShip.health + '%';
        }
        
        // Update player energy bar
        const energyBar = document.querySelector('.energy-reserves');
        if (energyBar) {
            const energyPercent = gameState.playerShip.getEnergyPercent();
            energyBar.style.width = energyPercent + '%';
        }
        
        // Update enemy health bar
        const enemyHealthBar = document.querySelector('.enemy-health');
        if (enemyHealthBar) {
            const enemyHealthPercent = gameState.enemyShip.getHealthPercent();
            enemyHealthBar.style.width = enemyHealthPercent + '%';
        }
    }

    updateWeaponStatus(playerShip) {
        Object.keys(this.weaponConfigs).forEach(weaponType => {
            const button = document.getElementById(weaponType + 'Btn');
            const statusElement = button.querySelector('.weapon-status');
            const energyCost = this.weaponConfigs[weaponType];
            
            if (playerShip.hasEnergy(energyCost)) {
                statusElement.textContent = 'Ready';
                statusElement.style.color = '#00FF00';
                statusElement.style.borderColor = '#00FF00';
                statusElement.style.backgroundColor = 'rgba(0, 255, 0, 0.1)';
                button.style.opacity = '1';
                button.style.cursor = 'pointer';
            } else {
                statusElement.textContent = 'No Energy';
                statusElement.style.color = '#FF4444';
                statusElement.style.borderColor = '#FF4444';
                statusElement.style.backgroundColor = 'rgba(255, 68, 68, 0.1)';
                button.style.opacity = '0.6';
                button.style.cursor = 'not-allowed';
            }
        });
    }

    updateLevelDisplay(level) {
        const levelNumber = document.querySelector('.level-number');
        if (levelNumber) {
            levelNumber.textContent = level;
        }
    }

    updateEnemyLabel(level) {
        const enemyLabel = document.querySelector('.enemy-label');
        if (enemyLabel) {
            enemyLabel.textContent = `Enemy (Level ${level}):`;
        }
    }

    // Screen management
    showStartupScreen() {
        const startupScreen = document.getElementById('startupScreen');
        const gameContainer = document.getElementById('gameContainer');
        
        startupScreen.style.display = 'flex';
        gameContainer.style.display = 'none';
    }

    showGameScreen() {
        const startupScreen = document.getElementById('startupScreen');
        const gameContainer = document.getElementById('gameContainer');
        
        startupScreen.style.display = 'none';
        gameContainer.style.display = 'flex';
    }

    // Button visibility management
    showRestartButton() {
        const restartContainer = document.getElementById('restartContainer');
        if (restartContainer) {
            restartContainer.style.display = 'block';
        }
    }

    hideRestartButton() {
        const restartContainer = document.getElementById('restartContainer');
        if (restartContainer) {
            restartContainer.style.display = 'none';
        }
    }

    showContinueButton() {
        const continueContainer = document.getElementById('continueContainer');
        if (continueContainer) {
            continueContainer.style.display = 'block';
        }
    }

    hideContinueButton() {
        const continueContainer = document.getElementById('continueContainer');
        if (continueContainer) {
            continueContainer.style.display = 'none';
        }
    }

    // Panel management
    showWeaponsPanel() {
        const weaponsPanel = document.querySelector('.weapons-panel');
        if (weaponsPanel) {
            weaponsPanel.style.display = 'block';
        }
    }

    hideWeaponsPanel() {
        const weaponsPanel = document.querySelector('.weapons-panel');
        if (weaponsPanel) {
            weaponsPanel.style.display = 'none';
        }
    }

    // Reset UI to initial state
    reset() {
        this.hideRestartButton();
        this.hideContinueButton();
        this.showWeaponsPanel();
        this.updateLevelDisplay(1);
        this.updateEnemyLabel(1);
        
        // Reset health bars to full
        const hullBar = document.querySelector('.hull-integrity');
        const energyBar = document.querySelector('.energy-reserves');
        const enemyHealthBar = document.querySelector('.enemy-health');
        
        if (hullBar) hullBar.style.width = '100%';
        if (energyBar) energyBar.style.width = '100%';
        if (enemyHealthBar) enemyHealthBar.style.width = '100%';
    }

    // Show loading state
    showLoading() {
        // This could be enhanced with a loading overlay
        console.log('Loading game assets...');
    }

    hideLoading() {
        console.log('Assets loaded, game ready!');
    }
}
