import { MathChallenge } from './MathChallenge.js';
import { AssetManager } from './AssetManager.js';
import { Ship, createPlayerShip, createEnemyShip } from './Ship.js';
import { WeaponSystem } from './WeaponSystem.js';
import { GameRenderer } from './GameRenderer.js';
import { GameUI } from './GameUI.js';

class StarTrekGame {
    constructor() {
        this.canvas = document.getElementById('gameCanvas');
        this.width = this.canvas.width;
        this.height = this.canvas.height;
        
        // Initialize modules
        this.assetManager = new AssetManager();
        this.mathChallenge = new MathChallenge();
        this.weaponSystem = new WeaponSystem(this.assetManager);
        this.renderer = new GameRenderer(this.canvas, this.assetManager);
        this.ui = new GameUI();
        
        // Game state
        this.level = 1;
        this.gameOver = false;
        this.gameWon = false;
        this.gameOverMessage = '';
        this.levelCompleted = false;
        this.gameStarted = false;
        this.gamePaused = false;
        
        // Enemy shooting system
        this.lastEnemyShot = Date.now();
        this.enemyShootInterval = 6000; // Base interval in milliseconds
        
        // Initialize game
        this.initializeShips();
        this.initializeControls();
        
        // Start render loop
        this.renderLoop();
    }
    

    
    initializeShips() {
        // Create ships using factory functions
        this.playerShip = createPlayerShip(this.width, this.height);
        this.enemyShip = createEnemyShip(this.width, this.height, this.level);
    }
    
    initializeControls() {
        // Set up UI callbacks
        this.ui.setWeaponCallbacks({
            fire: (weaponType) => this.fireWeapon(weaponType)
        });
        
        this.ui.setRepairCallbacks({
            show: (repairType) => this.showMathChallenge(repairType)
        });
        
        this.ui.setGameCallbacks({
            start: () => this.startGame(),
            restart: () => this.restartGame(),
            continue: () => this.continueToNextLevel()
        });
    }
    
    startGame() {
        // Show game screen
        this.ui.showGameScreen();
        
        // Start the game
        this.gameStarted = true;
        
        console.log('Game started!');
    }
    

    
    showMathChallenge(repairType) {
        // Don't show if game not started, game over, or panel already open
        if (!this.gameStarted || this.gameOver || this.mathChallenge.isPanelOpen()) {
            return;
        }
        
        // Pause the game
        this.gamePaused = true;
        
        // Show math challenge with callbacks
        this.mathChallenge.showChallenge(
            repairType,
            (type) => this.applyRepair(type),      // onSuccess
            (type) => this.applyPenalty(type),     // onFailure
            () => this.closeMathPanel()            // onCancel
        );
    }
    

    
    applyRepair(repairType) {
        if (repairType === 'hull') {
            this.playerShip.repairHull();
        } else if (repairType === 'energy') {
            this.playerShip.rechargeEnergy();
        }
    }
    
    applyPenalty(repairType) {
        if (repairType === 'hull') {
            const wasDestroyed = this.playerShip.takeDamage(1);
            
            // Check if player is defeated due to penalty
            if (wasDestroyed) {
                this.gameOver = true;
                this.gameOverMessage = 'MISSION FAILED';
                this.ui.showRestartButton();
                this.mathChallenge.closePanel();
                console.log('Game Over due to penalty!');
            }
        } else if (repairType === 'energy') {
            this.playerShip.consumeEnergy(1);
            console.log(`Penalty applied to energy! Energy: ${this.playerShip.energy}`);
        }
    }
    
    closeMathPanel() {
        // Resume game
        this.gamePaused = false;
        
        console.log('Math challenge closed, game resumed');
    }

    fireWeapon(weaponType) {
        // Don't allow firing if game is over, level is completed, not started, or paused
        if (this.gameOver || this.levelCompleted || !this.gameStarted || this.gamePaused) {
            return;
        }
        
        // Fire weapon using weapon system
        const success = this.weaponSystem.fireWeapon(weaponType, this.playerShip, this.enemyShip);
        
        if (success && this.enemyShip.isDestroyed()) {
            this.levelCompleted = true;
            // Show continue button after a brief delay for explosion
            setTimeout(() => {
                this.ui.showContinueButton();
            }, 1000);
        }
    }
    

    
    levelUp() {
        this.level++;
        
        // Level up enemy ship
        this.enemyShip.levelUp(this.level);
        
        // Increase enemy firing rate based on level (faster = shorter interval)
        this.enemyShootInterval = Math.max(800, 6000 - (this.level - 1) * 300);
        
        console.log(`Level ${this.level}! Enemy has ${this.enemyShip.maxHealth} health. Firing rate: ${this.enemyShootInterval}ms`);
    }
    
    fireEnemyWeapon() {
        this.weaponSystem.fireEnemyWeapon(this.enemyShip, this.playerShip, this.level);
        
        // Check if player was destroyed
        if (this.playerShip.isDestroyed()) {
            this.gameOver = true;
            this.gameOverMessage = 'MISSION FAILED';
            setTimeout(() => {
                this.ui.showRestartButton();
            }, 1000);
        }
    }
    

    
    updateShips() {
        // Don't update ship positions if game is over, not started, or paused
        if (this.gameOver || !this.gameStarted || this.gamePaused) {
            return;
        }
        
        // Update ship movements
        this.playerShip.update();
        this.enemyShip.update();
    }
    
    updateProjectiles() {
        // Don't update projectiles if game is over, not started, or paused
        if (this.gameOver || !this.gameStarted || this.gamePaused) {
            return;
        }
        
        // Check if enemy should fire (only if level not completed)
        const currentTime = Date.now();
        if (currentTime - this.lastEnemyShot > this.enemyShootInterval && 
            this.enemyShip.isAlive() && !this.levelCompleted) {
            this.fireEnemyWeapon();
            this.lastEnemyShot = currentTime;
        }
        
        // Update weapon system
        this.weaponSystem.update(this.playerShip, this.enemyShip);
    }
    

    
    render() {
        // Create game state object for renderer
        const gameState = {
            level: this.level,
            gameOver: this.gameOver,
            gameOverMessage: this.gameOverMessage,
            playerShip: this.playerShip,
            enemyShip: this.enemyShip,
            weaponSystem: this.weaponSystem
        };
        
        // Render using the GameRenderer
        this.renderer.render(gameState);
        
        // Update UI
        this.ui.updateUI(gameState);
    }
    

    
    continueToNextLevel() {
        this.levelCompleted = false;
        this.levelUp();
        
        // Reset player to full health and energy
        this.playerShip.reset({
            x: this.width / 2,
            y: this.height - 120,
            health: 100,
            energy: 100,
            direction: 1
        });
        
        // Reset enemy ship to initial position (levelUp already handled health)
        this.enemyShip.reset({
            x: this.width / 2,
            y: 90,
            health: this.enemyShip.maxHealth, // Use the new maxHealth from levelUp
            direction: -1
        });
        
        // Reset enemy shooting timer
        this.lastEnemyShot = Date.now();
        
        // Clear weapon system
        this.weaponSystem.clear();
        
        console.log(`Advanced to Level ${this.level}! Enemy Health: ${this.enemyShip.maxHealth}`);
    }
    
    restartGame() {
        // Reset game state
        this.gameOver = false;
        this.gameWon = false;
        this.gameOverMessage = '';
        this.level = 1;
        this.levelCompleted = false;
        this.gameStarted = false;
        this.gamePaused = false;
        
        // Close math panel if open
        if (this.mathChallenge.isPanelOpen()) {
            this.mathChallenge.closePanel();
        }
        
        // Show startup screen
        this.ui.showStartupScreen();
        
        // Reset ships
        this.playerShip.reset({
            x: this.width / 2,
            y: this.height - 120,
            health: 100,
            energy: 100,
            direction: 1
        });
        
        this.enemyShip.reset({
            x: this.width / 2,
            y: 90,
            health: 100,
            direction: -1
        });
        
        // Reset enemy shooting
        this.lastEnemyShot = Date.now();
        this.enemyShootInterval = 6000;
        
        // Clear weapon system
        this.weaponSystem.clear();
        
        // Reset UI
        this.ui.reset();
        
        console.log('Game restarted!');
    }
    
    renderLoop() {
        // Always update renderer animations
        this.renderer.update();
        
        // Only update game logic if game is started and not paused
        if (this.gameStarted && !this.gamePaused) {
            this.updateShips();
            this.updateProjectiles();
        }
        
        // Always render
        this.render();
        
        requestAnimationFrame(() => this.renderLoop());
    }
}

// Initialize game when page loads
window.addEventListener('load', () => {
    new StarTrekGame();
});
