/**
 * MathChallenge - Handles math problem generation and repair system
 */
export class MathChallenge {
    constructor() {
        this.currentChallenge = null;
        this.panelOpen = false;
        this.initializeEventListeners();
    }

    initializeEventListeners() {
        const cancelBtn = document.getElementById('cancelMathBtn');
        const optionA = document.getElementById('optionA');
        const optionB = document.getElementById('optionB');
        const optionC = document.getElementById('optionC');
        const optionD = document.getElementById('optionD');
        
        cancelBtn.addEventListener('click', () => this.closePanel());
        
        // Add event listeners for multiple choice options
        optionA.addEventListener('click', () => this.selectOption('A'));
        optionB.addEventListener('click', () => this.selectOption('B'));
        optionC.addEventListener('click', () => this.selectOption('C'));
        optionD.addEventListener('click', () => this.selectOption('D'));
        
        // Allow Escape key to cancel
        document.addEventListener('keydown', (event) => {
            if (event.key === 'Escape' && this.panelOpen) {
                this.closePanel();
            }
        });
    }

    showChallenge(repairType, onSuccess, onFailure, onCancel) {
        // Don't show if panel already open
        if (this.panelOpen) {
            return false;
        }
        
        this.panelOpen = true;
        
        // Generate random multiplication question
        const num1 = Math.floor(Math.random() * 9) + 2; // 2-10
        const num2 = Math.floor(Math.random() * 9) + 2; // 2-10
        const correctAnswer = num1 * num2;
        
        // Generate multiple choice options
        const options = this.generateOptions(correctAnswer);
        
        this.currentChallenge = {
            num1,
            num2,
            correctAnswer,
            repairType,
            options,
            correctOptionLetter: options.correctLetter,
            onSuccess,
            onFailure,
            onCancel
        };
        
        this.updatePanelContent();
        this.showPanel();
        
        console.log(`Math challenge: ${num1} × ${num2} = ${correctAnswer} (for ${repairType})`);
        console.log(`Options: A=${options.A}, B=${options.B}, C=${options.C}, D=${options.D}, Correct=${options.correctLetter}`);
        
        return true;
    }

    generateOptions(correctAnswer) {
        // Generate 3 wrong answers
        const wrongAnswers = [];
        
        // Create wrong answers by adding/subtracting small amounts and multiplying by close factors
        while (wrongAnswers.length < 3) {
            let wrongAnswer;
            const method = Math.floor(Math.random() * 4);
            
            switch (method) {
                case 0: // Add a small amount
                    wrongAnswer = correctAnswer + Math.floor(Math.random() * 10) + 1;
                    break;
                case 1: // Subtract a small amount
                    wrongAnswer = correctAnswer - Math.floor(Math.random() * 10) - 1;
                    break;
                case 2: // Multiply by a close factor
                    wrongAnswer = Math.floor(correctAnswer * (0.8 + Math.random() * 0.4));
                    break;
                case 3: // Use a common mistake (like one number * 10)
                    wrongAnswer = Math.floor(correctAnswer / 10) * 10 + Math.floor(Math.random() * 10);
                    break;
            }
            
            // Make sure it's positive and different from correct answer and other wrong answers
            if (wrongAnswer > 0 && wrongAnswer !== correctAnswer && !wrongAnswers.includes(wrongAnswer)) {
                wrongAnswers.push(wrongAnswer);
            }
        }
        
        // Create array of all options
        const allOptions = [correctAnswer, ...wrongAnswers];
        
        // Shuffle the options
        for (let i = allOptions.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [allOptions[i], allOptions[j]] = [allOptions[j], allOptions[i]];
        }
        
        // Assign to letters
        const options = {
            A: allOptions[0],
            B: allOptions[1],
            C: allOptions[2],
            D: allOptions[3]
        };
        
        // Find which letter has the correct answer
        const letters = ['A', 'B', 'C', 'D'];
        options.correctLetter = letters.find(letter => options[letter] === correctAnswer);
        
        return options;
    }

    updatePanelContent() {
        if (!this.currentChallenge) return;
        
        const { num1, num2, repairType, options } = this.currentChallenge;
        
        // Update panel content
        const questionElement = document.getElementById('mathQuestion');
        const descriptionElement = document.getElementById('mathDescription');
        const feedbackElement = document.getElementById('mathFeedback');
        
        questionElement.textContent = `${num1} × ${num2} = ?`;
        descriptionElement.textContent = `Complete this calculation to repair your ${repairType === 'hull' ? 'hull integrity' : 'energy reserves'}`;
        feedbackElement.textContent = '';
        feedbackElement.className = 'math-feedback';
        
        // Update option buttons
        document.getElementById('optionA').querySelector('.option-value').textContent = options.A;
        document.getElementById('optionB').querySelector('.option-value').textContent = options.B;
        document.getElementById('optionC').querySelector('.option-value').textContent = options.C;
        document.getElementById('optionD').querySelector('.option-value').textContent = options.D;
        
        // Reset option button styles
        ['optionA', 'optionB', 'optionC', 'optionD'].forEach(id => {
            const btn = document.getElementById(id);
            btn.classList.remove('correct', 'incorrect');
        });
    }

    selectOption(selectedLetter) {
        if (!this.currentChallenge) return;
        
        const feedbackElement = document.getElementById('mathFeedback');
        const selectedBtn = document.getElementById(`option${selectedLetter}`);
        
        // Reset all button styles first
        ['optionA', 'optionB', 'optionC', 'optionD'].forEach(id => {
            const btn = document.getElementById(id);
            btn.classList.remove('correct', 'incorrect');
        });
        
        if (selectedLetter === this.currentChallenge.correctOptionLetter) {
            // Correct answer!
            selectedBtn.classList.add('correct');
            feedbackElement.textContent = 'CORRECT! Repair applied.';
            feedbackElement.className = 'math-feedback success';
            
            // Call success callback and close immediately
            if (this.currentChallenge.onSuccess) {
                this.currentChallenge.onSuccess(this.currentChallenge.repairType);
            }
            this.closePanel();
        } else {
            // Wrong answer - apply penalty
            selectedBtn.classList.add('incorrect');
            feedbackElement.textContent = 'INCORRECT! Penalty applied. Try again.';
            feedbackElement.className = 'math-feedback error';
            
            // Call failure callback
            if (this.currentChallenge.onFailure) {
                this.currentChallenge.onFailure(this.currentChallenge.repairType);
            }
            
            // Show correct answer briefly
            const correctBtn = document.getElementById(`option${this.currentChallenge.correctOptionLetter}`);
            correctBtn.classList.add('correct');
            
            // Reset after 2 seconds
            setTimeout(() => {
                selectedBtn.classList.remove('incorrect');
                correctBtn.classList.remove('correct');
                feedbackElement.textContent = '';
                feedbackElement.className = 'math-feedback';
            }, 2000);
        }
    }

    showPanel() {
        // Hide weapons panel and show math challenge panel
        const weaponsPanel = document.querySelector('.weapons-panel');
        const mathPanel = document.getElementById('mathChallengePanel');
        weaponsPanel.style.display = 'none';
        mathPanel.style.display = 'block';
    }

    closePanel() {
        // Hide math challenge panel and show weapons panel
        const mathPanel = document.getElementById('mathChallengePanel');
        const weaponsPanel = document.querySelector('.weapons-panel');
        mathPanel.style.display = 'none';
        weaponsPanel.style.display = 'block';
        
        // Call cancel callback if provided
        if (this.currentChallenge && this.currentChallenge.onCancel) {
            this.currentChallenge.onCancel();
        }
        
        // Reset state
        this.panelOpen = false;
        this.currentChallenge = null;
        
        console.log('Math challenge closed');
    }

    isPanelOpen() {
        return this.panelOpen;
    }

    getCurrentChallenge() {
        return this.currentChallenge;
    }
}
