/**
 * WeaponSystem - Handles weapons, projectiles, and combat mechanics
 */
export class WeaponSystem {
    constructor(assetManager) {
        this.assetManager = assetManager;
        this.projectiles = [];
        this.missMessages = [];
        this.explosions = [];
        this.shipExplosions = [];
        
        // Weapon configurations
        this.weaponConfigs = {
            photon: {
                energyCost: 25,
                damage: 15,
                color: '#ff0000',
                speed: 0.005,
                arcHeight: { min: 80, max: 200 }
            },
            phaser: {
                energyCost: 50,
                damage: 10,
                duration: 500,
                instant: true
            },
            quantum: {
                energyCost: 50,
                damage: 20,
                color: '#ffffff',
                speed: 0.006,
                arcHeight: { min: 100, max: 250 },
                hasParticles: true
            }
        };
    }

    // Fire a weapon from a ship
    fireWeapon(weaponType, shooterShip, targetShip) {
        const config = this.weaponConfigs[weaponType];
        if (!config) {
            console.error(`Unknown weapon type: ${weaponType}`);
            return false;
        }

        // Check energy cost for player ships
        if (shooterShip.isPlayer && !shooterShip.hasEnergy(config.energyCost)) {
            console.log(`Insufficient energy for ${weaponType}! Need ${config.energyCost}, have ${shooterShip.energy}`);
            return false;
        }

        // Consume energy
        if (shooterShip.isPlayer) {
            shooterShip.consumeEnergy(config.energyCost);
        }

        // Handle phaser (instant hit)
        if (config.instant) {
            this.createPhaserBeam(shooterShip, targetShip, config);
            this.assetManager.playWeaponSound(weaponType);
            
            // Apply damage immediately for phaser
            const wasDestroyed = targetShip.takeDamage(config.damage);
            if (wasDestroyed) {
                this.createShipExplosion(targetShip);
            }
            return true;
        }

        // Create projectile for torpedoes
        const projectile = this.createProjectile(weaponType, shooterShip, targetShip, config);
        this.projectiles.push(projectile);
        
        // Play weapon sound
        if (shooterShip.isPlayer) {
            this.assetManager.playWeaponSound(weaponType);
        } else {
            this.assetManager.playEnemyWeaponSound('torpedo');
        }

        return true;
    }

    // Fire enemy weapons
    fireEnemyWeapon(enemyShip, playerShip, level = 1) {
        const weaponTypes = ['phaser', 'torpedo'];
        const weaponType = weaponTypes[Math.floor(Math.random() * weaponTypes.length)];
        
        if (weaponType === 'phaser') {
            // Enemy phaser beam
            const beam = {
                shooterShip: enemyShip,
                targetShip: playerShip,
                duration: 500,
                startTime: Date.now(),
                type: 'enemy_phaser',
                damage: 15 + level * 2
            };
            
            this.projectiles.push(beam);
            this.assetManager.playEnemyWeaponSound('phaser');
            
            // Apply damage immediately for enemy phaser
            const wasDestroyed = playerShip.takeDamage(beam.damage);
            if (wasDestroyed) {
                this.createShipExplosion(playerShip);
            }
        } else {
            // Enemy torpedo
            const projectile = {
                x: enemyShip.x,
                y: enemyShip.y + enemyShip.height/2 + 10,
                startX: enemyShip.x,
                startY: enemyShip.y + enemyShip.height/2 + 10,
                targetX: playerShip.x,
                targetY: playerShip.y,
                progress: 0,
                arcHeight: 60 + Math.random() * 80,
                arcDirection: Math.random() < 0.5 ? -1 : 1,
                speed: 0.008 + level * 0.001,
                width: 4,
                height: 12,
                damage: 20 + level * 3,
                color: '#00ff00',
                type: 'enemy_torpedo',
                trail: [],
                useCircularArc: true,
                isEnemyProjectile: true
            };
            
            this.projectiles.push(projectile);
            this.assetManager.playEnemyWeaponSound('torpedo');
        }
        
        console.log(`Enemy fired ${weaponType}!`);
    }

    createProjectile(weaponType, shooterShip, targetShip, config) {
        const isEnemy = shooterShip.isEnemy;
        const startY = isEnemy ? 
            shooterShip.y + shooterShip.height/2 + 10 : 
            shooterShip.y - shooterShip.height/2 - 10;

        const projectile = {
            x: shooterShip.x,
            y: startY,
            startX: shooterShip.x,
            startY: startY,
            targetX: targetShip.x,
            targetY: targetShip.y,
            progress: 0,
            arcHeight: config.arcHeight.min + Math.random() * (config.arcHeight.max - config.arcHeight.min),
            arcDirection: Math.random() < 0.5 ? -1 : 1,
            speed: config.speed,
            width: weaponType === 'quantum' ? 6 : 4,
            height: weaponType === 'quantum' ? 20 : 15,
            damage: config.damage,
            color: config.color,
            type: weaponType,
            trail: [],
            useCircularArc: true,
            isEnemyProjectile: isEnemy
        };

        // Add particles for quantum torpedoes
        if (config.hasParticles) {
            projectile.particles = [];
        }

        return projectile;
    }

    createPhaserBeam(shooterShip, targetShip, config) {
        const isEnemy = shooterShip.isEnemy;
        const beam = {
            shooterShip: shooterShip,
            targetShip: targetShip,
            duration: config.duration,
            startTime: Date.now(),
            type: isEnemy ? 'enemy_phaser' : 'phaser'
        };
        
        this.projectiles.push(beam);
    }

    // Update all projectiles
    update(playerShip, enemyShip) {
        this.updateProjectiles(playerShip, enemyShip);
        this.updateMissMessages();
        this.updateShipExplosions();
    }

    updateProjectiles(playerShip, enemyShip) {
        this.projectiles = this.projectiles.filter(projectile => {
            // Handle phaser beams
            if (projectile.type === 'phaser' || projectile.type === 'enemy_phaser') {
                return Date.now() - projectile.startTime < projectile.duration;
            }
            
            // Handle explosion particles
            if (projectile.type === 'explosion') {
                projectile.x += projectile.vx;
                projectile.y += projectile.vy;
                projectile.life--;
                return projectile.life > 0;
            }
            
            // Handle torpedoes with arc movement
            if (projectile.progress !== undefined) {
                this.updateProjectileArc(projectile);
                
                // Add trail effect
                if (projectile.trail) {
                    projectile.trail.push({x: projectile.x, y: projectile.y});
                    const maxTrailLength = projectile.useCircularArc ? 30 : 8;
                    if (projectile.trail.length > maxTrailLength) {
                        projectile.trail.shift();
                    }
                }
                
                // Add particles for quantum torpedoes
                this.updateQuantumParticles(projectile);
                
                // Check collisions
                const collision = this.checkCollisions(projectile, playerShip, enemyShip);
                if (collision) {
                    return false; // Remove projectile
                }
                
                // Remove if arc completed without hit
                if (projectile.progress >= 1.0) {
                    this.createMissMessage(projectile, playerShip, enemyShip);
                    return false;
                }
            }
            
            return true;
        });
    }

    updateProjectileArc(projectile) {
        projectile.progress += projectile.speed;
        
        // Store previous position for rotation
        const prevX = projectile.x;
        const prevY = projectile.y;
        
        const t = projectile.progress;
        
        if (projectile.useCircularArc) {
            // Cache expensive calculations on first use
            if (!projectile.cached) {
                const deltaX = projectile.targetX - projectile.startX;
                const deltaY = projectile.targetY - projectile.startY;
                const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);
                
                projectile.cached = {
                    deltaX: deltaX,
                    deltaY: deltaY,
                    perpX: -deltaY / distance,
                    perpY: deltaX / distance
                };
            }
            
            // Create sideways rainbow arc using cached values
            const baseX = projectile.startX + projectile.cached.deltaX * t;
            const baseY = projectile.startY + projectile.cached.deltaY * t;
            
            // Arc displacement: sine wave for smooth curve
            const arcOffset = Math.sin(Math.PI * t) * projectile.arcHeight * projectile.arcDirection;
            
            projectile.x = baseX + projectile.cached.perpX * arcOffset;
            projectile.y = baseY + projectile.cached.perpY * arcOffset;
        }
        
        // Calculate rotation angle
        if (prevX !== undefined && prevY !== undefined) {
            const dx = projectile.x - prevX;
            const dy = projectile.y - prevY;
            projectile.rotation = Math.atan2(dy, dx);
        }
        
        // Ensure final position
        if (projectile.progress >= 1.0) {
            projectile.x = projectile.targetX;
            projectile.y = projectile.targetY;
        }
    }

    updateQuantumParticles(projectile) {
        if (projectile.type === 'quantum' && projectile.particles) {
            // Add new particles
            for (let i = 0; i < 3; i++) {
                projectile.particles.push({
                    x: projectile.x + (Math.random() - 0.5) * 10,
                    y: projectile.y + (Math.random() - 0.5) * 10,
                    vx: (Math.random() - 0.5) * 2,
                    vy: (Math.random() - 0.5) * 2,
                    life: 30,
                    maxLife: 30
                });
            }
            
            // Update existing particles
            projectile.particles = projectile.particles.filter(particle => {
                particle.x += particle.vx;
                particle.y += particle.vy;
                particle.life--;
                return particle.life > 0;
            });
        }
    }

    checkCollisions(projectile, playerShip, enemyShip) {
        const targetShip = projectile.isEnemyProjectile ? playerShip : enemyShip;
        
        if (targetShip.contains(projectile.x, projectile.y)) {
            const wasDestroyed = targetShip.takeDamage(projectile.damage);
            this.createExplosion(projectile.x, projectile.y);
            
            if (wasDestroyed) {
                this.createShipExplosion(targetShip);
            }
            
            return true; // Collision detected
        }
        
        return false;
    }

    createExplosion(x, y) {
        this.assetManager.playExplosionSound();
        
        // Add explosion particles
        for (let i = 0; i < 20; i++) {
            this.projectiles.push({
                x: x + (Math.random() - 0.5) * 20,
                y: y + (Math.random() - 0.5) * 20,
                vx: (Math.random() - 0.5) * 4,
                vy: (Math.random() - 0.5) * 4,
                life: 30,
                maxLife: 30,
                size: Math.random() * 3 + 1,
                type: 'explosion'
            });
        }
    }

    createShipExplosion(ship) {
        this.assetManager.playExplosionSound();
        
        const explosionData = {
            x: ship.x,
            y: ship.y,
            width: ship.width,
            height: ship.height,
            particles: [],
            startTime: Date.now(),
            duration: 3000,
            isPlayer: ship.isPlayer
        };
        
        // Create many explosion particles
        for (let i = 0; i < 80; i++) {
            explosionData.particles.push({
                x: ship.x + (Math.random() - 0.5) * ship.width,
                y: ship.y + (Math.random() - 0.5) * ship.height,
                vx: (Math.random() - 0.5) * 8,
                vy: (Math.random() - 0.5) * 8,
                life: 60 + Math.random() * 120,
                maxLife: 60 + Math.random() * 120,
                size: Math.random() * 6 + 2,
                color: Math.random() < 0.5 ? '#ff6600' : '#ffff00',
                type: 'shipExplosion'
            });
        }
        
        // Add secondary explosion waves
        for (let wave = 0; wave < 3; wave++) {
            setTimeout(() => {
                for (let i = 0; i < 30; i++) {
                    explosionData.particles.push({
                        x: ship.x + (Math.random() - 0.5) * ship.width * (1 + wave * 0.5),
                        y: ship.y + (Math.random() - 0.5) * ship.height * (1 + wave * 0.5),
                        vx: (Math.random() - 0.5) * (6 - wave),
                        vy: (Math.random() - 0.5) * (6 - wave),
                        life: 40 + Math.random() * 80,
                        maxLife: 40 + Math.random() * 80,
                        size: Math.random() * 4 + 1,
                        color: wave === 0 ? '#ff0000' : (wave === 1 ? '#ff6600' : '#ffff00'),
                        type: 'shipExplosion'
                    });
                }
                if (wave < 2) {
                    this.assetManager.playExplosionSound();
                }
            }, wave * 500);
        }
        
        this.shipExplosions.push(explosionData);
        console.log(`${ship.isPlayer ? 'Player' : 'Enemy'} ship explosion created!`);
    }

    createMissMessage(projectile, playerShip, enemyShip) {
        const targetShip = projectile.isEnemyProjectile ? playerShip : enemyShip;
        const messageX = targetShip.x + targetShip.width/2 + 20;
        const messageY = targetShip.y;
        
        this.missMessages.push({
            x: messageX,
            y: messageY,
            text: 'MISSED',
            life: 120,
            maxLife: 120,
            color: '#ff0000',
            fontSize: 16,
            startTime: Date.now()
        });
        
        console.log(`${projectile.isEnemyProjectile ? 'Enemy' : 'Player'} torpedo missed!`);
    }

    updateMissMessages() {
        this.missMessages = this.missMessages.filter(message => {
            message.life--;
            return message.life > 0;
        });
    }

    updateShipExplosions() {
        this.shipExplosions.forEach(explosion => {
            explosion.particles = explosion.particles.filter(particle => {
                particle.x += particle.vx;
                particle.y += particle.vy;
                particle.life--;
                
                // Add gravity effect
                particle.vy += 0.1;
                particle.vx *= 0.99;
                particle.vy *= 0.99;
                
                return particle.life > 0;
            });
        });
        
        // Remove finished explosions
        this.shipExplosions = this.shipExplosions.filter(explosion => {
            const elapsed = Date.now() - explosion.startTime;
            return elapsed < explosion.duration && explosion.particles.length > 0;
        });
    }

    // Clear all projectiles and effects
    clear() {
        this.projectiles = [];
        this.missMessages = [];
        this.explosions = [];
        this.shipExplosions = [];
    }

    // Getters for rendering
    getProjectiles() {
        return this.projectiles;
    }

    getMissMessages() {
        return this.missMessages;
    }

    getShipExplosions() {
        return this.shipExplosions;
    }
}
